import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './style.scss';
import { modalTypes } from '../../../../constant';
import { connect } from 'react-redux';


@connect(  (state, props) => {
    return {
      activeEffect: state.getIn(['config', 'activeEffect'])
    }
  }, {})
export default class ConfigPanel extends Component {

  static contextTypes = {
    showModal: PropTypes.func.isRequired
  };

  renderPropertyTab(){
    if (this.props.activeEffect) {
    return(<div className={style.propertyContainer}>
      <div className={style.durationContainer}>
        <div className={style.labelContainer}>Filter strength</div>
        <div>
          <select className = {style.propSelect}>
              <option value="0">0%</option>
              <option value="50">50%</option>
              <option value="100">100%</option>
          </select>
        </div>
      </div>
      <div className={style.removeContainer}>
        <a className={style.removeLinks}
          onMouseDown={e=>{
            e.preventDefault();
            this.onChange("removeLinks");
          }}
          >REMOVE</a>
      </div>
    </div>);
    } else {
      return "";
    }
  }

  render() {
    const effectList = {
      B1: "Black and white b1",
      B2: "Black and white b2",
      B3: "Black and white b3",
      B4: "Black and white b4",

      C1: "Black and white c1",
      C2: "Black and white c2",
      C3: "Black and white c3",
      C4: "Black and white c4",
    }

    var effectCode = "+";
    var effectName = "Pick a color filter"
    if (this.props.activeEffect){
      effectCode = this.props.activeEffect;
      effectName = effectList[effectCode];
    }
    return (<div className={style['container']} >
      <div className={style.filterRow} onMouseDown={e=>{
          e.preventDefault();
            this.context.showModal(
              modalTypes.SELECT_FILTER_MODAL,
              'FILTERS LIBRARY',
              'Choose a filter and apply to your image',
              {},
              '');
        }}>
        <span className={style.filterIcon}>{effectCode}</span>
        <span className={style.filterName}>{effectName}</span>
      </div>

      {this.renderPropertyTab()}

    </div>);
  }
}



