import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './image-libary.scss';
import { hideModal } from '../../../../redux/modules/modals';
import { connect } from 'react-redux';
import { changeActiveEffect } from '../../../../redux/modules/config-panel';

@connect(null, { hideModal })
class ImageLibraryModal extends Component {
  static contextTypes = {
    showModal: PropTypes.func.isRequired,
    hideModal: PropTypes.func.isRequired
  };

  onSelectImage(index){
    console.log("select image",index)
    this.props.hideModal();
  }

  render() {
    return (<div className={style.container}>
      <div className={style.controls}>
        <div className={style.sortBox}>

        </div>
        <div className={style.searchBox}>

        </div>
      </div>
      <div className={style.containerGrid}>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("B1")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("B2")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("B3")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("B4")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("C1")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("C2")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("C3")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
        <div className={style.effectCell} onClick={e=>{this.onSelectImage("C4")}}>
          <div className = {style.thumbnail}>
            <div className = {style.selectEffect}>SELECT</div>
          </div>
        </div>
      </div>
    </div>);
  }
}

export { ImageLibraryModal };



