import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  Editor as DraftJSEditor,
  EditorState,
  SelectionState,
  convertFromRaw,
  convertToRaw,
  RichUtils,
  CompositeDecorator
} from 'draft-js';
import styles from './draftjs.scss';
import FontTypePanel from '../../config-panel/panels/font-type-config-panel';
import FontStypePanel from '../../config-panel/panels/font-style-config-panel';
import FontAlignPanel from '../../config-panel/panels/font-align-config-panel';
import AnimationePanel from '../../config-panel/panels/animation-config-panel';
import LinkToPanel from '../../config-panel/panels/link-config-panel';

import { changeFontType, changeFontStyle, changeActiveUrl } from '../../../redux/modules/config-panel';
import { updateAnimationWidgets } from '../../../redux/modules/widgets';

import './draftjs.scss';
@connect(  (state, props) => {
    return {
      activeId: state.getIn(['config', 'id'])
    }
  }, {changeFontType, changeFontStyle, changeActiveUrl, updateAnimationWidgets})
class Editor extends Component {
  static propTypes = {};
  constructor(props, context) {
    super(props, context);
    this.state = {
      editorState: this.createEditorState(props)
    };
    this.handleKeyCommand = this.handleKeyCommand.bind(this);
    this.styleMap =  this._styleMap();
    this.justUpdateStyle = false;

    this.nameInput = false;
  }

   _styleMap = () => {
     return {
       UPPER:{
         textTransform: 'uppercase'
       },
       CAPITALIZE:{
         textTransform: 'capitalize'
       },
       LOWER:{
         textTransform: 'lowercase'
       }
      }
   }

  onConfigChange = (e) =>{
    if (e.fontType){
      var fontTypeChanged = null;
      var selection = this.state.editorState.getSelection();
      var startKey = selection.getStartKey();
      var endKey = selection.getEndKey();
      if (!startKey || !endKey) return;

      const blockType = this.state.editorState
            .getCurrentContent()
            .getBlockForKey(selection.getStartKey())
            .getType();
      if(blockType !== e){
        fontTypeChanged = RichUtils.toggleBlockType(
          this.state.editorState,
          e.fontType
        )
        this.handleContentChange(fontTypeChanged);
      }
      this.props.changeFontType(e.fontType);
    }

    if (e.fontStyle){
      var fontStypeChanged = RichUtils.toggleInlineStyle(
        this.state.editorState,
        e.fontStyle
      );
      this.handleContentChange(fontStypeChanged);;
      this.justUpdateStyle = true;
    }

    if (e.fontAlign){
      fontTypeChanged = RichUtils.toggleBlockType(
        this.state.editorState,
        e.fontAlign
      )
      this.handleContentChange(fontTypeChanged);
      this.props.changeFontType(e.fontAlign);
    }

    if (e.removeLinks){
      if (this.nameInput)  this.nameInput.focus();
      var newLinkState = RichUtils.toggleLink(
              this.state.editorState,
              this.state.editorState.getSelection(),
              null);
      this.handleContentChange(newLinkState);
    }

    if (e.activeLink){
      if (this.nameInput)  this.nameInput.focus();

      const contentState = this.state.editorState.getCurrentContent();
      const contentStateWithEntity = contentState.createEntity(
            'LINK',
            'MUTABLE',
            {url: e.activeLink}
          );
      const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
      const newEditorState = EditorState.set(this.state.editorState, { currentContent: contentStateWithEntity });
      var newLinkState = RichUtils.toggleLink(
              newEditorState,
              newEditorState.getSelection(),
              entityKey);
      this.handleContentChange(newLinkState);

    }

    if (e.animationConfig){
      var newConfig = {
        event: e.activeTabIndex,
        type: e.activeAnimation,
        duration: e.durationTime,
        delay: e.delayTime
      }
      this.props.updateAnimationWidgets(this.props.activeId, newConfig);
    }
  }

  handleDrop = () => {
    return 'handled';
  };

  handleKeyCommand = command => {
    var { editorState } = this.state;

    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      this.handleContentChange(newState);
      return 'handled';
    }
    return 'not-handled';
  };

  handleContentChange = editorState => {
    const currentContent = this.state.editorState.getCurrentContent();
    const newContent = editorState.getCurrentContent();
    this.selection = editorState.getSelection();
    if (currentContent !== newContent) {
      this.props.updateWidgets({
        dataPath: ['content'],
        value: convertToRaw(editorState.getCurrentContent())
      });
    }
    //
    // if (!this.selection.getHasFocus()  && this.justUpdateStyle) {
    //   this.selection = SelectionState.createEmpty();
    //   var newEditorState = EditorState.acceptSelection(this.state.editorState, this.selection);
    //   this.setState({editorState: newEditorState});
    // }
    this.setState({ editorState });
  };

  updateInlineStyle(){
    let selection = this.state.editorState.getSelection();
    if (selection.toJS().focusKey){
      const currentStyle = this.state.editorState.getCurrentInlineStyle();
      this.props.changeFontStyle(currentStyle.toJS());
    }
  }

  componentDidUpdate(){
    if (this.justUpdateStyle){
      if(!this.state.editorState) return;
      this.updateInlineStyle();
      this.justUpdateStyle = false;
    }
  }

  findLinkEntities(contentBlock, callback, contentState) {
    contentBlock.findEntityRanges(
      (character) => {
        const entityKey = character.getEntity();
        return (
          entityKey !== null &&
          contentState.getEntity(entityKey).getType() === 'LINK'
        );
      },
      callback
    );
  }

  createEditorState = (props, selection) => {
    const decorator = new CompositeDecorator([
      {
        strategy: this.findLinkEntities,
        component: Link,
      },
    ]);

    let editorState = EditorState.createWithContent(
      convertFromRaw(props.content)
    );
    editorState = EditorState.set(editorState, {decorator: decorator});
    return EditorState.acceptSelection(
      editorState,
      selection || SelectionState.createEmpty()
    );
  };

  componentWillReceiveProps = nextProps => {
    let content = convertFromRaw(nextProps.content);
    this.setState({
      editorState: EditorState.acceptSelection(
        EditorState.push(this.state.editorState, content),
        this.selection || SelectionState.createEmpty()
      )
    });
  };

  widgetFocus = () => {
    const content = this.state.editorState.getCurrentContent();
    const block = content.getLastBlock();
    const key = block.getKey();
    const len = block.getCharacterList().size;
    let selection = this.state.editorState.getSelection();
    selection = selection.withMutations(s => {
      s.set('anchorKey', key);
      s.set('anchorOffset', len);
      s.set('focusKey', key);
      s.set('focusOffset', len);
      s.set('hasFocus', true);
      return s;
    });
    const editorState = EditorState.forceSelection(
      this.state.editorState,
      selection
    );
    this.justUpdateStyle = true;
    this.setState({ editorState });
    this.registerConfigPanel();
  };

  registerConfigPanel = () => {
    let selection = this.state.editorState.getSelection();
    const contentState = this.state.editorState.getCurrentContent();
    const blockType = contentState.getBlockForKey(selection.getStartKey())
          .getType();
    this.props.changeFontType(blockType);

    const startKey = selection.getStartKey();
    const startOffset = selection.getStartOffset();

    if (startOffset){
      const blockWithLinkAtBeginning = contentState.getBlockForKey(startKey);
      const linkKey = blockWithLinkAtBeginning.getEntityAt(startOffset);
      let url = '';
      if (linkKey) {
        const linkInstance = contentState.getEntity(linkKey);
        url = linkInstance.getData().url;
      }
      this.props.changeActiveUrl(url);
    }


    this.props.setConfigPanel([
      FontTypePanel,
      FontStypePanel,
      FontAlignPanel,
      AnimationePanel,
      LinkToPanel
    ], this.onConfigChange);

    this.updateInlineStyle();
  };

  myBlockStyleFn(contentBlock) {
    var fontType = contentBlock.getType();
    return fontType;
  }

  render() {
    return (
      <div
        onClick={e => {
          this.registerConfigPanel();
          e.stopPropagation();
        }}
      >
        <DraftJSEditor
          ref={(input) => { this.nameInput = input; }}
          onFocus={this.props.onFocus}
          onBlur={this.props.onBlur}
          editorState={this.state.editorState}
          onChange={this.handleContentChange}
          handleKeyCommand={this.handleKeyCommand}
          handleDrop={this.handleDrop}
          customStyleMap={this.styleMap}
          blockStyleFn={this.myBlockStyleFn}
        />
      </div>
    );
  }
}

const Link = (props) => {
  const {url} = props.contentState.getEntity(props.entityKey).getData();
  return (
    <a href={url} className={styles.link}>
      {props.children}
    </a>
  );
};

export { Editor };



