import React, { Component } from 'react';
import { connect } from 'react-redux';
import style from './style.scss';
import  './style.scss';
import classnames from 'classnames';

function mapStateToProps(state) {
  return {
    fontType: state.getIn(['config', 'fontType'])
  };
}
@connect(mapStateToProps, {})
export default class ConfigPanel extends Component {
  constructor() {
    super();
    this.onChange = this._onChange.bind(this);
  }
  _onChange = fontAlign => {
    if (this.props.onChange) this.props.onChange({fontAlign});
  }
  render() {
    return (<div className={style['container']} >
          <InlineStyleControls
            fontType = {this.props.fontType}
            currentInlineStyle = {this.props.currentInlineStyle}
            onChange = {this.onChange}
            ></InlineStyleControls>
      </div>);
  }
}

class StyleButton extends React.Component {
  constructor() {
    super();
  }
  render() {
    return (
      <div
        className={this.props.className}
        onMouseDown = {this.props.onChange}
        >
        <div className={`icon icon-${this.props.label}`} />
      </div>
    );
  }
}

var BLOCK_ALIGN_STYLES = [
  {label: 'align-left', style: 'alignLeft', styleCode: 'alignLeft'},
  {label: 'align-center', style: 'alignCenter', styleCode: 'alignCenter'},
  {label: 'align-right', style: 'alignRight', styleCode: 'alignRight'},
  {label: 'justify-left', style: 'justifyLeft', styleCode: 'justifyLeft'},
  {label: 'justify-center', style: 'justifyCenter', styleCode: 'justifyCenter'},
  {label: 'justify-right', style: 'justifyRight', styleCode: 'justifyRight'},
];

const InlineStyleControls = (props) => {

  const setTextAlign = (e) => {
    var fontType = props.fontType;
    fontType = fontType.replace("alignLeft-","");
    fontType = fontType.replace("alignCenter-","");
    fontType = fontType.replace("alignRight-","");
    fontType = fontType.replace("justifyLeft-","");
    fontType = fontType.replace("justifyCenter-","");
    fontType = fontType.replace("justifyRight-","");
    return e + "-" + fontType;
  }

  const checkIsActive = (e) => {
    if (props.fontType && props.fontType.indexOf(e) !== -1) {
      return true;
    }
    if (e === "alignLeft" && props.fontType.indexOf("align") === -1 && props.fontType.indexOf("justify") === -1)
      return true;
    else
      return false;
  }

  return (
    <div className={style.inlineTable}>
      {BLOCK_ALIGN_STYLES.map((type) =>
        <StyleButton
          key = {type.style}
          label={type.label}
          className={classnames(style.inlineCell, style[type.style], {[style.active]:checkIsActive(type.styleCode)}   )}
          onChange = {()=>props.onChange(setTextAlign(type.styleCode))}
        />
      )}
    </div>
  );
};



