import React, { Component } from 'react';
import { connect } from 'react-redux';
import style from './style.scss';
import  './style.scss';
import classnames from 'classnames';

function mapStateToProps(state) {
  return {
    typeName: state.getIn(['config', 'fontType'])
  };
}
@connect(mapStateToProps, {})
export default class ConfigPanel extends Component {
  constructor() {
    super();
    this.onChange = this._onChange.bind(this);
  }
  _onChange = fontType => {
    if (this.props.onChange) this.props.onChange({fontType});
  }
  render() {
    return (<div className={style['container']} >
          <BlockStyleControls
            typeName = {this.props.typeName}
            onChange = {this.onChange}
            ></BlockStyleControls>
      </div>);
  }
}

class CellButton extends React.Component {
  constructor() {
    super();
  }
  render() {
    return (
      <span
        className={this.props.className}
        onMouseDown = {this.props.onChange}
        >
        {this.props.label}
      </span>
    );
  }
}

const BLOCK_TYPES = [
  {label: 'BIG HEADING', style: 'headerOne', styleCode: 'header-one'},
  {label: 'HEADING', style: 'headerTwo', styleCode: 'header-two'},
  {label: 'Text', style: 'normal', styleCode: 'unstyled'},
  {label: 'Quote', style: 'blockquote', styleCode: 'blockquote'},
];

const BlockStyleControls = (props) => {
  return (
    <div >
      {BLOCK_TYPES.map((type) =>
        <CellButton
          key = {type.style}
          label={type.label}
          className={classnames(style.blockStyle,style[type.style],{[style.active]:  props.typeName.indexOf(type.styleCode) !== -1})}
          onChange = {()=>props.onChange(type.styleCode)}
        />
      )}
    </div>
  );
};



