import React, { Component } from 'react';
import style from './style.scss';
import { connect } from 'react-redux';
import classnames from 'classnames';
import { Map, is } from 'immutable';

function mapStateToProps(state) {
  var activeId = state.getIn(['config', 'id']);
  return {
    widget: state.getIn(['widgets', activeId]).toJS(),
  };
}
@connect(mapStateToProps, {})
export default class ConfigPanel extends Component {

  constructor() {
    super();
    this.state = {
      animationConfig: true,
      activeTabIndex: 0,
      activeAnimation: 0,
      durationTime: 0,
      delayTime: 0,
    }
    this.updateInside = false;
  }

  onDurationTimeChange(value){
    this.updateInside = true;
    this.setState({durationTime: value});
  }

  onDelayTimeChange(value){
    this.updateInside = true;
    this.setState({delayTime: value});
  }

  onAnimationTypeChange(index){
    this.updateInside = true;
    this.setState({activeTabIndex: index});
  }

  onAnimationChange(index){
    this.updateInside = true;
    this.setState({activeAnimation: index});
  }

  _componentUpdateState(){
    if (this.props.widget){
      var configs = this.props.widget.data.animation;
      var currentConfigs = {
        event: this.state.activeTabIndex,
        type: this.state.activeAnimation,
        duration: this.state.durationTime,
        delay: this.state.delayTime
      };
      if (JSON.stringify(currentConfigs)!== JSON.stringify(configs))
        this.setState({
          activeTabIndex: configs.event,
          activeAnimation: configs.type,
          durationTime: configs.duration,
          delayTime: configs.delay,
        });
    } else {
      this.setState({
        animationConfig: true,
        activeTabIndex: 0,
        activeAnimation: 0,
        durationTime: 0,
        delayTime: 0,
      });
    }
  }

  componentWillMount(){
    this._componentUpdateState();
  }

  componentDidUpdate(){
    this._componentUpdateState();

    if (this.updateInside){
      this.updateInside = false;
      if (this.props.onChange) this.props.onChange(this.state);
    }
  }

  renderAnimationTypeList(){
    if (this.state.activeTabIndex=="on_enter_viewport"){
      return (<div  >
        <div className = {style.animationOptionContainer}>
          <div
            className={classnames(style.animationOption, {[style.active]: this.state.activeAnimation === 'fade' })}
            onMouseDown = {e=>{
              e.preventDefault();
              this.onAnimationChange("fade");
            }}
            >
              <div className={style.rect}></div>
              <div>Fade</div>
            </div>
          <div
            className={classnames(style.animationOption, {[style.active]: this.state.activeAnimation === 'slide' })}
            onMouseDown = {e=>{
              e.preventDefault();
              this.onAnimationChange("slide");
            }}
            >
            <div className={style.rect}></div>
            <div>Slide</div>
          </div>
          <div
            className={classnames(style.animationOption, {[style.active]: this.state.activeAnimation === 'swipe' })}
            onMouseDown = {e=>{
              e.preventDefault();
              this.onAnimationChange("swipe");
            }}
            >
            <div className={style.rect}></div>
            <div>Swipe</div>
          </div>
        </div>


        <div className={style.propertyContainer}>
          <div className={style.durationContainer}>
            <div className={style.labelContainer}>Duration(s)</div>
            <div>
              <select className = {style.propSelect}
                onChange = { event => this.onDurationTimeChange( event.target.value ) }
                value = {this.state.durationTime} >
                  <option value="1.5">1.5</option>
                  <option value="2.5">2.5</option>
                  <option value="3.5">3.5</option>
              </select>
            </div>
          </div>
          <div className={style.delayContainer}>
            <div className={style.labelContainer}>Delay(s)</div>
            <div>
              <select className = {style.propSelect}
                onChange = { event => this.onDelayTimeChange( event.target.value ) }
                value = {this.state.delayTime} >
                <option value="0">0</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
              </select>
            </div>
          </div>
        </div>

      </div>);
    }
    return "";
  }

  render() {
    return (<div className={style.containerTypeAnimation} >
      <select className = {style.containerSelect}
        onChange = { event => this.onAnimationTypeChange( event.target.value ) }
        value = {this.state.activeTabIndex} >
          <option value="none">None</option>
          <option value="on_enter_viewport">On enter viewport</option>
      </select>
      {this.renderAnimationTypeList()}
    </div>);
  }
}



