import { Map } from 'immutable';
import { DESELECT_OBJECT, SET_ACTIVE_WIDGET, CHANGE_ACTIVE_EFFECT,
  CHANGE_FONT_TYPE , CHANGE_FONT_STYLE, CHANGE_ACTIVE_URL} from './../../constant';

export const deselect = () => ({
  type: DESELECT_OBJECT
});

export const setActiveWidget = id => ({
  type: SET_ACTIVE_WIDGET,
  id
});

export const changeFontType = fontType => ({
  type: CHANGE_FONT_TYPE,
  fontType
});

export const changeFontStyle = fontStyle => ({
  type: CHANGE_FONT_STYLE,
  fontStyle
});

export const changeActiveUrl = activeUrl => ({
  type: CHANGE_ACTIVE_URL,
  activeUrl
})


export const changeActiveEffect = activeEffect => ({
  type: CHANGE_ACTIVE_EFFECT,
  activeEffect
});

export default (state = new Map({ id: '', fontType: '' , fontStyle: '[]', activeUrl: ''}), action) => {
  switch (action.type) {
    case DESELECT_OBJECT: {
      return new Map({ id: '' });
    }
    case SET_ACTIVE_WIDGET: {
      return state.set('id', action.id);
    }
    case CHANGE_FONT_TYPE:{
      return state.set('fontType',action.fontType)
    }
    case CHANGE_FONT_STYLE:{
      return state.set('fontStyle',JSON.stringify(action.fontStyle))
    }
    case CHANGE_ACTIVE_URL:{
      return state.set('activeUrl',action.activeUrl)
    }
    case CHANGE_ACTIVE_EFFECT: {
      return state.set('activeEffect',action.activeEffect)
    }
    default:
      return state;
  }
};



