import React, { Component } from 'react';
import PropTypes from 'prop-types';
import style from './select-image-modal.scss';
import { hideModal } from '../../../../redux/modules/modals';
import { connect } from 'react-redux';
import { changeActiveEffect } from '../../../../redux/modules/config-panel';
import { modalTypes } from '../../../../constant';

@connect(null, { hideModal, changeActiveEffect })
class SelectImageModal extends Component {
  static contextTypes = {
    showModal: PropTypes.func.isRequired,
    hideModal: PropTypes.func.isRequired
  };

  selectSource(index){
    this.props.hideModal();
    if (index==1){
      this.context.showModal(
        modalTypes.SELECT_IMAGE_LIBRARY,
        'IMAGES LIBRARY',
        'Choose a images from your upload library',
        {},
        'body');
    } else {
      this.refs.fileUploader.click();
      // do something to upload file anf get path
      // change widget state
    }
  }

  render() {
    return (<div className={style.container} >
      <input type="file" id="file" ref="fileUploader" accept=".jpg,.png" style={{display: "none"}}/>
      <span className = {style.sourceButton}
        onMouseDown = {
          e => {
            e.preventDefault();
            this.selectSource(0);
          }
        }
        >
        <div className = {style.buttonIcon}>
          <div className="icon icon-computer"></div>
        </div>
        <div className = {style.buttonLabel}>My computer</div>
      </span>
      <span  className = {style.sourceButton}
        onMouseDown = {
          e => {
            e.preventDefault();
            this.selectSource(1);
          }
        }
        >
        <div className = {style.buttonIcon}>
          <div className="icon icon-library"></div>
        </div>
        <div className = {style.buttonLabel}>Image Library</div>
      </span>
    </div>);
  }
}

export { SelectImageModal };



