import React, { Component } from 'react';
import { connect } from 'react-redux';
import style from './style.scss';
import classnames from 'classnames';

function mapStateToProps(state) {
  return {
    activeUrl: state.getIn(['config', 'activeUrl'])
  };
}
@connect(mapStateToProps, {})
export default class ConfigPanel extends Component {
  constructor() {
    super();
    this.state = {
      activeTabIndex: 0,
      activeUrl: "",
      pageOptions: [
        {
          name: 'Choose a page',
          value: '',
        },
        {
          name: 'Homepage',
          value: 'https://mywebsite.company/index.html',
        },
        {
          name: 'Page One',
          value: 'https://mywebsite.company/page-one.html',
        },
        {
          name: 'Page Two',
          value: 'https://mywebsite.company/page-two.html',
        },
      ],
    };
    this.onChange = this._onChange.bind(this);
  }
  _onChange(action){
    if (action === "removeLinks"){
      if (this.props.onChange) this.props.onChange({
        removeLinks: true
      });
    } else {
      if (this.props.onChange) this.props.onChange({
        activeLink: this.state.activeUrl
      });
    }
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.activeUrl){
      this.handleTabClick(1);
      this.state.activeUrl = nextProps.activeUrl;
    } else {
      this.handleTabClick(0);
      this.state.activeUrl = "";
    }
  }

  handleTabClick(tabIndex) {
    this.setState({
        activeTabIndex: tabIndex,
    });
  }

  renderActiveTabCotent(){

    const createItem = (item, key) =>
      <option
        key={key}
        value={item.value}
      >
        {item.name}
      </option>;

    if (this.state.activeTabIndex === 0){
      return (  <div className={style.tabContent}>
          <select
            onChange={event => this.setState({ activeUrl: event.target.value })}
            value={this.state.activeUrl}
            >
            {this.state.pageOptions.map(createItem)}
          </select>
        </div>
      );
    }
    if (this.state.activeTabIndex === 1){
      // this.state.value="https://google.com";
      return (
        <div className={style.tabContent}>
          <input type="text" placeholder="Website URL ..."
            onChange={event => this.setState({ activeUrl: event.target.value })}
            value={this.state.activeUrl}
            key="0"
            ></input>
        </div>
      );
    }
    if (this.state.activeTabIndex === 2){
      // this.state.value="mailto:someone@example.com?Subject=Hello";
      return (
        <div className={style.tabContent}>
          <input type="text" placeholder="Mail to ..."
            onChange={event => this.setState({ activeUrl: event.target.value })}
            value={this.state.activeUrl}
            key="1"
            ></input>
        </div>
      );
    }
  }

  render() {
    return (<div className={style['container']}>
      <div className={style.tabLabels}>
        <div onMouseDown={
            (e)=>{
              e.preventDefault();
              this.handleTabClick(0);
            }
          } className={classnames(style.tabLabel, {[style.active]:this.state.activeTabIndex===0}   )} > <span className={`${style.icon} icon icon-page`}></span> Page </div>
        <div onMouseDown={
            (e)=>{
              e.preventDefault();
              this.handleTabClick(1);
            }
          } className={classnames(style.tabLabel, {[style.active]:this.state.activeTabIndex===1}   )}  > <span className={`${style.icon} icon icon-website`}></span> Website </div>
        <div onMouseDown={
            (e)=>{
              e.preventDefault();
              this.handleTabClick(2);
            }
          } className={classnames(style.tabLabel, {[style.active]:this.state.activeTabIndex===2}   )}  > <span className={`${style.icon} icon icon-email`}> </span> Email </div>
      </div>
      {this.renderActiveTabCotent()}
      <div className={style.tabLabels}>
        <a className={style.removeLinks}
          onMouseDown={e=>{
            e.preventDefault();
            this.onChange("removeLinks");
          }}
          >Remove links</a>
        <div className={style.createLink}
          onMouseDown={e=>{
            e.preventDefault();
            this.onChange("createLink");
          }}
          > CREATE </div>
      </div>
    </div>);
  }
}



