import React, { Component } from 'react';
import { connect } from 'react-redux';
import style from './style.scss';
import  './style.scss';
import classnames from 'classnames';

function mapStateToProps(state) {
  return {
    currentInlineStyle: JSON.parse(state.getIn(['config', 'fontStyle']))
  };
}
@connect(mapStateToProps, {})
export default class ConfigPanel extends Component {
  constructor() {
    super();
    this.onChange = this._onChange.bind(this);
  }
  _onChange = fontStyle => {
    if (this.props.onChange) this.props.onChange({fontStyle});
  }
  render() {
    return (<div className={style['container']} >
          <InlineStyleControls
            currentInlineStyle = {this.props.currentInlineStyle}
            onChange = {this.onChange}
            ></InlineStyleControls>
      </div>);
  }
}

class StyleButton extends React.Component {
  constructor() {
    super();
  }
  render() {
    return (
      <div
        className={this.props.className}
        onMouseDown = {(event) => {
          event.preventDefault();
          this.props.onChange();
          }}
        >
        {this.props.label}
      </div>
    );
  }
}

var INLINE_STYLES = [
  {label: 'B', style: 'bold', styleCode: 'BOLD'},
  {label: 'I', style: 'italic', styleCode: 'ITALIC'},
  {label: 'U', style: 'underline', styleCode: 'UNDERLINE'},
  {label: 'AA', style: 'upper', styleCode: 'UPPER'},
  {label: 'Aa', style: 'capitalize', styleCode: 'CAPITALIZE'},
  {label: 'aa', style: 'lower', styleCode: 'LOWER'},
];

const InlineStyleControls = (props) => {
  return (
    <div className={style.inlineTable}>
      {INLINE_STYLES.map((type) =>
        <StyleButton
          key = {type.style}
          label={type.label}
          className={classnames(style.inlineCell, style[type.style], {[style.active]:props.currentInlineStyle.indexOf(type.styleCode) !== -1}   )}
          onChange = {()=>props.onChange(type.styleCode)}
        />
      )}
    </div>
  );
};



