import React, { Component } from 'react';
import style from './style.scss';
import { connect } from 'react-redux';
import classnames from 'classnames';
export default class ConfigPanel extends Component {
  constructor() {
    super();
    this.state = {
      pictureSize: '',
    }
  }
  onAnimationChange(index){
    // console.log("change size", index);
    // this.state.pictureSize = index;
    this.setState({
      pictureSize: index
    });
  }
  render() {
    return (<div className={style['container']} >
    <div className={style.containerLabel}>Image Radio</div>
    <div className = {style.animationOptionContainer}>
      <div
        className={classnames(style.animationOption, {[style.active]: this.state.pictureSize === '4:3' })}
        onMouseDown = {e=>{
          e.preventDefault();
          this.onAnimationChange("4:3");
        }}
        >
            <div className={style.ratioNumber}>4:3</div>
            <div className={style.ratioLabel}>Horizontal</div>
          </div>
        <div
          className={classnames(style.animationOption, {[style.active]: this.state.pictureSize === '3:4' })}
          onMouseDown = {e=>{
            e.preventDefault();
            this.onAnimationChange("3:4");
          }}
          >
          <div className={style.ratioNumber}>3:4</div>
          <div className={style.ratioLabel}>Vertical</div>
        </div>
        <div
          className={classnames(style.animationOption, {[style.active]: this.state.pictureSize === '1:1' })}
          onMouseDown = {e=>{
            e.preventDefault();
            this.onAnimationChange("1:1");
          }}
          >
          <div className={style.ratioNumber}>1:1</div>
          <div className={style.ratioLabel}>Square</div>
        </div>

        <div
          className={classnames(style.animationOption, {[style.active]: this.state.pictureSize === '16:9' })}
          onMouseDown = {e=>{
            e.preventDefault();
            this.onAnimationChange("16:9");
          }}
          >
          <div className={style.ratioNumber}>16:9</div>
          <div className={style.ratioLabel}>Custom</div>
        </div>
        <div
          className={classnames(style.animationOption, {[style.active]: this.state.pictureSize === '' })}
          onMouseDown = {e=>{
            e.preventDefault();
            this.onAnimationChange("");
          }}
          >
          <div className={style.ratioNumber}></div>
          <div className={style.ratioLabel}>Original</div>
        </div>
      </div>
    </div>);
  }
}



