import React, { Component } from 'react';
import { connect } from 'react-redux';
import SelectImagePanel from '../../config-panel/panels/select-image-panel';
import SizingPanel from '../../config-panel/panels/sizing-config-panel';
import FilterPanel from '../../config-panel/panels/filter-config-panel';
import AnimationPanel from '../../config-panel/panels/animation-config-panel';
import LinkToPanel from '../../config-panel/panels/link-config-panel';

import style from './image-picker.scss';
import { changeActiveUrl } from '../../../redux/modules/config-panel';
import { updateAnimationWidgets, updateLinkWidget } from '../../../redux/modules/widgets';

@connect(  (state, props) => {
  var activeId =  state.getIn(['config', 'id']);
    return {
      activeId: activeId,
      activeWidget: state.getIn(['widgets', activeId, 'data']),
      activeUrl: state.getIn(['config','activeUrl'])
    }
  }, {changeActiveUrl, updateAnimationWidgets, updateLinkWidget} )
class Editor extends Component {
  static propTypes = {};
  constructor(props, context) {
    super(props, context);
    this.onConfigChange = this.onConfigChange.bind(this);
  }
  onConfigChange(e){

    if (e.removeLinks){
      this.props.changeActiveUrl("");
      this.props.updateLinkWidget(this.props.activeId, "");
    }

    if (e.activeLink){
      this.props.changeActiveUrl(e.activeLink);
      this.props.updateLinkWidget(this.props.activeId, e.activeLink);
    }

    if (e.animationConfig){
      var newConfig = {
        event: e.activeTabIndex,
        type: e.activeAnimation,
        duration: e.durationTime,
        delay: e.delayTime
      }
      this.props.updateAnimationWidgets(this.props.activeId, newConfig);
    }
  }

  componentDidUpdate(){
    if (this.props.activeWidget){
      var activeWidget = this.props.activeWidget.toJS();
      if (this.props.activeUrl!=activeWidget.link){
        this.props.changeActiveUrl(activeWidget.link);
      }
    }
  }

  widgetFocus = () => {
    this.props.setConfigPanel([
      SelectImagePanel,
      SizingPanel,
      FilterPanel,
      AnimationPanel,
      LinkToPanel
    ], this.onConfigChange);
  };

  render() {
    return (
      <div className={style.container} onClick={e=>{
          e.stopPropagation();
          this.widgetFocus();
        }}>
        <img src={this.props.src} alt="demo" />
      </div>
    );
  }
}
export { Editor };



